/* ---------- toggle --------------------------*/
.toggle {
  position: relative;
  max-width: 270px;
}

.toggle__label {
  display: grid;
  grid-template-columns: 40px auto;
  grid-gap: 0 13px;
  font-size: 0.875rem;
