.toggle__input:checked + .toggle__fake {
  border: 1px solid #BC9CFF;
}
.toggle__input:checked + .toggle__fake::after {
  transform: translateX(22px);
  background-color: #BC9CFF;
}

.toggle__fake {
  display: inline-block;
  width: 40px;
  height: 20px;
  border-radius: 20px;
  border: 1px solid #C7C7CF;
  position: relative;
  grid-column: 1/2;
  grid-row: 1/2;
}
.toggle__fake::after {
  content: " ";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 100%;
  background-color: #C7C7CF;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  transform: translateX(4px);
  transition: 0.3s;
}
.toggle__fake:hover {
  cursor: pointer;
}

