  position: relative;
}
.bullet-list__item::before {
  content: " ";
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #C7C7CF;
  position: absolute;
  left: -20px;
  top: 11px;
}

/* ---------- bullet-list --------------------------*/
/* ---------- comfort --------------------------*/
.comfort {
  max-width: 270px;
}

.comfort__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 22px 0;
  border-bottom: 1px solid #C7C7CF;
}

.comfrot__block-text {
  display: inline-block;
  max-width: 200px;
  width: 100%;
}

.comfort__heading {
  text-transform: uppercase;
  margin-bottom: 13px;
}

.comfort__text {
  font-size: 0.875rem;
  font-family: "MontserratRegular";
  color: #575871;
  margin: 0;
}

/* ---------- comfort --------------------------*/
/* ---------- review --------------------------*/
.review {
  max-width: 720px;
  display: grid;
  grid-template-columns: 80% 1fr;
  grid-template-rows: auto auto;
  grid-gap: 20px 0;
}

.review__human {
  grid-column: 1/2;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 60px 1fr;
  grid-template-rows: 32px 32px;
}

.review__human-image {
  grid-column: 1/2;
  grid-row: 1/3;
  align-self: center;
  width: 50px;
  height: 50px;
  overflow: hidden;
  position: relative;
}
.review__human-image img {
  top: -6px;
  left: -18px;
  position: absolute;
}

.review__human-name {
  align-self: end;
}
.review__human-name h3 {
  color: #575871;
  font-size: 0.875rem;
}

.review__human-online {
  align-self: center;
}
.review__human-online p {
  font-family: "MontserratRegular";
  color: #575871;
  font-size: 0.875rem;
  margin: 0;
}

.review__main {
  grid-row: 2/3;
  grid-column: 1/3;
  display: flex;
}

.review__like {
  width: 100px;
  padding-top: 4px;
  padding-left: 5px;
}

.review__human-text {
  display: inline-block;
  max-width: 660px;
  margin-left: 15px;
}
.review__human-text p {
  font-family: "MontserratRegular";
  color: #575871;
  font-size: 0.875rem;
  line-height: 24px;
  margin: 0;
}

/* ---------- review --------------------------*/